r los coagulantes y los floculantes con el agua residual y formar sólidos que puedan flotar. Los componentes principales incluyen una estructura metálica, una entrada, válvulas de muestreo, válvulas de inyección, tuberías y una salida. Las tuberías están configuradas de manera ascendente y serpenteante para reducir la cantidad de espacio necesario para brindar un tiempo de reacción química suficiente (FRC Systems, 2015). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/27.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Mecanismo – funcionamiento ")]),_c('p',[_vm._v("Lo que ocurre dentro de un floculador es un concepto bastante simple, se bombea agua hacia del floculador, inmediatamente después de la entrada se da inicio a la dosificación química, generalmente coagulantes, mediante válvulas de inyección. Más adelante se inyecta un polímero floculante y se mezcla en las secciones subsiguientes del floculador. Algunas unidades incluyen un medidor de pH en línea y puntos de inyección para agentes neutralizantes (FRC Systems, 2015), ver figura 17. ")])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 17")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Coagulantes y floculantes ")])]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/28.png"),"alt":"Texto que describa la imagen"}}),_c('figcaption',[_vm._v("Nota. Tomado de Acoua tecnología (2020). https://acquatecnologiaperu.com/producto/coagulantes-y-floculantes ")])])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Dosificación química ")]),_c('p',[_vm._v("Se puede variar de acuerdo con el tipo de floculante, pues permite que las partículas coloidales desestabilizadas o las partículas formadas durante la etapa de coagulación se ensamblen en agregados y luego se puedan separar del medio acuoso; entre ellos se encuentran los floculantes catiónicos y los aniónicos (FRC systems, 2015). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('h4',[_vm._v("Preparación de los floculantes aniónicos ")]),_c('p',[_vm._v("Los floculantes aniónicos son preparados a base de copolímeros de acrilamida y ácido acrílico, con carga iónica negativa. Se unen con cargas catiónicas residuales sobre coagulantes adsorbidos a coloides coagulados. Su poder floculante intrínseco aumenta con el peso molecular. Las poliacrilamidas tienen el peso molecular más alto en el intervalo de 10-20 millones. Otros polímeros presentan propiedades específicas para condiciones específicas (Ingeniería Liquid technologies, s.f.).")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Floculantes catiónicos ")]),_c('p',[_vm._v("Se basan en copolímeros de poliacrilamida que proporcionan una amplia gama de ionicidades además de los pesos moleculares necesarios para lograr el máximo rendimiento en cada aplicación. De acuerdo con Acoua tecnología (2020), puede haber varias cargas en un floculante. Hay polímeros catiónicos, basados en el nitrógeno, polímeros aniónicos, basados en los iones del carboxilato y los polianfolitos, que llevan cargas positivas y negativas. ")])]),_c('div',{staticClass:"col-lg-6"},[_c('h4',[_vm._v("Desestabilización de partículas ")]),_c('p',[_vm._v("Los principales coagulantes y floculantes utilizados para desestabilizar las partículas y producir floculantes en agua son: ")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"p-4",staticStyle:{"background-color":"#fef7e2"}},[_c('ul',{staticClass:"lista-ul--color fw-bold"},[_c('li',[_c('i',{staticClass:"fas fa-check"}),_vm._v("Sulfato de aluminio. ")]),_c('li',[_c('i',{staticClass:"fas fa-check"}),_vm._v("Aluminato de sodio. ")]),