ficial (filtros lentos) o con alta carga superficial (filtros rápidos), en medios porosos (pastas arcillosas, papel de filtro) o en medios granulares (arena, antracita, granate o combinados), con flujo ascendente, descendente y mixto (parte ascendente y parte descendente). Por otro lado, el filtro puede trabajar a presión o por gravedad, según sea la magnitud de la carga hidráulica que exista sobre el lecho filtrante (Otero, 2006). ")])]),_c('div',{staticClass:"col-md-4"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/31.png"),"alt":""}})])])]),_c('div',{staticClass:"py-4 py-md-5 px-4",staticStyle:{"background-color":"#f3f9ff"},attrs:{"titulo":"Clasificación de los filtros"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Los filtros pueden clasificarse de la siguiente manera, ver tabla 2. ")])]),_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Tabla 2")]),_c('span',{staticClass:"fst-italic"},[_vm._v("Velocidad de filtración")])]),_c('div',{staticClass:"tabla-e text-center mb-5"},[_c('table',[_c('thead',[_c('tr',{staticStyle:{"background-color":"#badc61"}},[_c('th',[_vm._v("Velocidad de filtración")]),_c('th',[_vm._v("Medio filtrante usado")]),_c('th',[_vm._v("Carga sobre el lecho")])])]),_c('tbody',{staticClass:"bg-white"},[_c('tr',[_c('td',{attrs:{"rowspan":"4"}},[_vm._v("Rápidos: "),_c('br'),_vm._v("120-360 m3 "),_c('br'),_vm._v("/(m2 "),_c('br'),_vm._v("día ")]),_c('td',[_vm._v("Arena ")]),_c('td',{attrs:{"rowspan":"2"}},[_vm._v("Por gravedad")])]),_c('tr',[_c('td',[_vm._v("Antracita")])]),_c('tr',[_c('td',[_vm._v("Mixtos: "),_c('br'),_vm._v("Antracita (35 - 50 cm) "),_c('br'),_vm._v("Arena (20-35 cm)")]),_c('td',{attrs:{"rowspan":"2"}},[_vm._v("Por presión")])]),_c('tr',[_c('td',[_vm._v("Mixtos: arena, "),_c('br'),_vm._v("antracita, granate")])]),_c('tr',[_c('td',[_vm._v("Lentos: "),_c('br'),_vm._v("7-14 m3 "),_c('br'),_vm._v("/(m2 "),_c('br'),_vm._v("día) ")]),_c('td',[_vm._v("Arena "),_c('br'),_vm._v("(h= 60-100 cm) ")]),_c('td',[_vm._v("Por gravedad")])])])])])])])]),_c('div',{staticClass:"py-4 py-md-5 px-4",staticStyle:{"background-color":"#f3f9ff"},attrs:{"titulo":"Otras clasificaciones"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("De igual forma, los filtros también se clasifican por medio filtrante, es decir, el material de relleno en cantidad, capas y características según las necesidades del agua a tratar, ver figura 19.")])]),_c('div',{staticClass:"col-md-4"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 19")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Velocidad de filtración")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/32.png"),"alt":""}})])])])]),_c('Separador'),_vm._m(30),_vm._m(31),_vm._m(32),_vm._m(33),_vm._m(34),_vm._m(35),_vm._m(36),_c('TabsB',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"¿Dónde sucede el proceso? ","icono":require('@/assets/curso/temas/tema2/37.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('p',[_vm._v("El proceso se lleva a cabo en un reactor completamente cerrado. Los lodos se introducen en el reactor de forma continua o intermitente, y permanecen en su interior durante períodos de tiempo variables. El lodo estabilizado, que se extrae del proceso continuo o intermitentemente, tiene un bajo contenido en materia orgánica y patógenos, y no es putrescible. Los dos tipos de digestores anaeróbicos más empleados son los de alta y baja carga. En el proceso de digestión de baja carga no se suele calentar, ni mezclar el contenido del digestor, y los tiempos de detención oscilan entre 30 y 60 días. En los procesos de digestión de alta carga el contenido del digestor se calienta y mezcla completamente (Aguamarket, 2017), ver figura 23. ")])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 23")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Digestor anaerobio para lodos")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/39.png"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Equimar (s.f.). http://equimar.mx/our-services/digestores-anaerobios-para-lodos/ ")])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Etapas de la conversión biológica ","icono":require('@/assets/curso/temas/tema2/37.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('p',[_vm._v("La conversión biológica de la materia orgánica de los lodos parece que se produce en tres etapas. El primer paso del proceso comporta la transformación por vía enzimática (hidrólisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energía y de carbono celular. El segundo paso (acidogénesis) implica la conversión bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanogénesis) supone la conversión bacteriana de los compuestos intermedios en productos finales más simples, principalmente metano y dióxido de carbono. En un digestor, la conversión de los lodos orgánicos y de los residuos se lleva a cabo mediante la acción conjunta de diferentes organismos anaerobios (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/40.png"),"alt":""}})])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Hidrolización","icono":require('@/assets/curso/temas/tema2/37.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Un grupo de microorganismos se ocupa de la hidrolización de los polímeros orgánicos y de los lípidos para formar elementos estructurales básicos como los monosacáridos, los aminoácidos y los compuestos relacionados con estos. Un segundo grupo de bacterias anaerobias fermenta los productos de la descomposición para producir ácidos orgánicos simples, de los que el que se presenta con mayor frecuencia en los digestores orgánicos es el ácido acético. Este grupo de microorganismos, que reciben el nombre de no metanogénicos, está formado por bacterias facultativas y anaerobias estrictas, aunque de forma colectiva se conocen como bacterias \"formadoras de ácidos (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/41.png"),"alt":""}})])])])]),_vm._m(37),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Se trata de un sistema de depuración biológico aerobio cuyo funcionamiento se basa en hacer circular a través de un medio poroso, aire y agua residual. Están configurados en forma de cultivo fijo no sumergido y compuestos por un lecho, normalmente de gran diámetro, que puede estar formado por materiales naturales –cantos rodados, coque metalúrgico, escoria o antracita- o plásticos, y en cuya superficie se aplican de forma uniforme los líquidos residuales por medio de pulverizadores, y en forma de lluvia (SMA, 2015), ver figuras 24 y 25.")]),_vm._m(38),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En la parte inferior del lecho se sitúan el sistema de drenaje para la salida del efluente del proceso y el sistema de entrada de aire para la ventilación. Esta puede llevarse a cabo de forma natural, lo que exige para un correcto funcionamiento diferencias de temperatura superiores a 2ºC entre el aire -agua o forzada–, suele emplearse solo cuando el tamaño del filtro es demasiado grande y no se puede garantizar la ventilación natural. Cabe destacar que el lecho debe permanecer aireado y con un caudal suficiente de percolación, que permita arrastrar las fracciones de biopelícula erosionadas o desprendidas, y sin saturación de agua. De esta forma, se puede prevenir, en caso de cargas altas, la formación de charcos en su superficie (SMA, 2015), ver figura 26. ")]),_vm._m(39),_vm._m(40),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Los contactores biológicos rotativos están basados en los procesos biológicos aerobios. Estos requieren un contacto íntimo entre el agua residual, la biomasa activa y el oxígeno. Consisten en una serie de placas o discos colocados en un eje horizontal que giran lentamente dentro del tanque que contiene el agua residual, con un 49% de la superficie sumergida. Sobre la superficie de los discos se fija la biomasa que se encuentra alternativamente en contacto con el agua residual y el aire. Esto posibilita la ingestión de la materia orgánica por parte de los microorganismos y su posterior degradación en presencia del oxígeno del aire (Cyclusid, 2020), ver figura 27. ")]),_vm._m(41),_vm._m(42),_vm._m(43),_vm._m(44),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Son reactores discontinuos en los que el agua residual se mezcla con un lodo biológico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeración/reacción y clarificación. La tecnología de los reactores biológicos secuenciales es una variante optimizada de la tecnología convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biológicos secuenciales SBR consta de al menos cuatro procesos cíclicos: llenado, aireación, anoxia, decantación y vaciado, tanto de efluente como de los lodos. Esta tecnología es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocasiones, la tecnología más apropiada para la industria, ver figura 30 y 31. ")]),_vm._m(45),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}