uellas otras sustancias contaminantes no eliminadas en los tratamientos secundarios, como por ejemplo, los nutrientes, fósforo y nitrógeno. Estos procesos son de naturaleza biológica o fisicoquímica, siendo el proceso unitario más empleado el tratamiento fisicoquímico. Este consta de una coagulación – floculación y una decantación (Cyclusid depuración industrial, s.f.). ")]),_vm._m(59),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"tarjeta color-primario p-3 mb-5"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(60),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(61),_c('div',{staticClass:"col mb-3 mb-sm-0"},[_c('a',{staticClass:"boton color-acento-botones",attrs:{"href":_vm.obtenerLink('downloads/anexo1.pdf'),"target":"_blank","type":"application/pdf"}},[_c('span',[_vm._v("Descargar ")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])])],1)],1)}
