e sedimentación se presentan en los desarenadores, en los sedimentadores y en los presedimentadores como paso previo a la coagulación en las plantas de filtración rápida y también en sedimentadores como paso previo a la filtración lenta. ")])]),_c('div',{staticClass:"tarjeta w-100 p-4 mb-3",staticStyle:{"background-color":"#e2eacb"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("• Sedimentación de partículas floculentas:")]),_vm._v(" partículas floculentas son aquellas producidas por la aglomeración de partículas coloides desestabilizadas a consecuencia de la aplicación de agentes químicos. A diferencia de las partículas discretas, las características de este tipo de partículas —forma, tamaño, densidad— sí cambian durante la caída; se denomina sedimentación floculenta o decantación al proceso de depósito de partículas floculentas. Este tipo de sedimentación se presenta en la clarificación de aguas como proceso intermedio entre la coagulación-floculación y la filtración rápida. ")])]),_c('div',{staticClass:"tarjeta w-100 p-4 mb-3",staticStyle:{"background-color":"#e2eacb"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("• Sedimentación por caída libre e interferida:")]),_vm._v(" cuando existe una baja concentración de partículas en el agua, estas se depositan sin interferir y se denomina a este fenómeno caída libre. En cambio, cuando hay altas concentraciones de partículas se producen colisiones que las mantienen en una posición fija y ocurre un depósito masivo en lugar de individual. A este proceso de sedimentación se le denomina depósito o caída interferida o sedimentación zonal. Cuando las partículas ya en contacto forman una masa compacta que inhibe una mayor consolidación se produce una compresión o zona de compresión. Este tipo de sedimentación se presenta en los concentradores de lodos de las unidades de decantación con manto de lodos. ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justi