ad que la del fluido, por ascenso de esta hasta la superficie del fluido, ya que en este caso las fuerzas que tiran hacia arriba (rozamiento y empuje del líquido) superan a la fuerza de la gravedad. Se generan pequeñas burbujas de gas (aire), que se asociarán a las partículas presentes en el agua y serán elevadas hasta la superficie, donde son arrastradas y sacadas del sistema (Acuatecnia S.A.S., 2018), ver figura 14. ")])]),_c('div',{staticClass:"col-lg-6 col-8",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 14")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Sistema de flotación")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/21.png"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Aguas Sigma (2016). https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJB-E5CFFX1Cehh4NVp4a-E9Rer2hlmr0MSMTMPRjVTKW2M3U9 ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta color-primario rounded-pill d-inline-block m-auto p-3 mb-4"},[_c('h4',{staticClass:"mb-0"},[_vm._v("Coagulación-floculación")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-6 mb-lg-0 mb-3",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("En muchos casos parte de la materia en suspensión está formada por partículas de muy pequeño tamaño, lo que conforma una suspensión coloidal. Estas suspensiones coloidales suelen ser muy estables, en muchas ocasiones debido a interacciones eléctricas entre las partículas. Por tanto, tienen una velocidad de sedimentación extremadamente lenta, por lo que haría inviable un tratamiento mecánico clásico. Una forma de mejorar la eficacia de todos los sistemas de eliminación de materia en suspensión es la adición de ciertos reactivos químicos que, en primer lugar, desestabilicen la suspensión coloidal (coagulación) y a continuación favorezcan la floculación de estas para obtener partículas fácilmente sedimentables (Acuatecnia S.A.S., 2018), ver figura 16. ")])]),_c('div',{staticClass:"col-lg-6 col-8",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 16")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Floculadores ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/26.png"),"alt":"Texto que describa la imagen"}}),_c('figcaption',[_vm._v("Nota. Tomado de Eathisa (2018). http://eathisa.com/productos/floculadores/ ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta color-primario rounded-pill d-inline-block m-auto p-3 mb-4"},[_c('h4',{staticClass:"mb-0"},[_vm._v("Filtración")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"rounded-2 p-5",staticStyle:{"background-color":"#dee9f0"}},[_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 18 ")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Filtración ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/29.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Pedraza (2017). Filtración de aguas residuales")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_2_3","data-aos":"fade-right"}},[_c('h2',[_vm._v("2.3 Tratamientos secundarios del agua residual ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._se