amente lodos activados o de filtros percoladores; (2) mezclas de lodos activados o de filtros percoladores con lodos primarios, o (3) lodo biológico en exceso de plantas de tratamiento de lodos activados sin sedimentación primaria. Actualmente, suelen emplearse dos variantes del proceso de digestión aerobia: el sistema convencional y el sistema con oxígeno puro, aunque también se ha empleado la digestión aerobia termófila (Aguamarket, 2017), ver figura 20. ")])]),_c('div',{staticClass:"col-lg-5 col-8"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 20")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Tratamiento aerobio")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/34.png"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Global Water & Energy (2020). http://www.globalwe.com/es/soluciones/aguas-residuales/tratamiento-aerobio/ ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-1 p-5 mb-5 mn"},[_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-5"},[_c('h4',[_vm._v("Digestión aeróbica convencional")]),_c('p',[_vm._v("En esta el lodo se airea durante un largo período de tiempo en un estanque abierto, sin calefacción, empleando difusores convencionales o aireadores superficiales. El proceso se puede llevar a cabo de manera continua o discontinua. En plantas de pequeño tamaño se emplea el sistema discontinuo, en el que el lodo se airea y se mezcla completamente durante un largo período de tiempo, dejándose sedimentar a continuación en el interior del mismo estanque (Aguamarket, 2017), ver figura 21.")]),_c('div',{staticClass:"d-lg-none d-block mb-4"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 21")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Tanque aerobio TAE ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/35.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Ajover (s.f.). http://www.ajover.co/wp-content/uploads/2019/02/fichatecnicasistemasepticoecoajoverajover.pdf")])]),_c('h4',[_vm._v("Digestión con oxígeno y digestión aeróbica termófila ")]),_c('p',[_vm._v("En los sistemas continuos, la decantación y concentración del lodo se realiza en un estanque independiente. La digestión con oxígeno puro es una modificación del proceso de digestión aerobia en el que se sustituye el aire por oxígeno puro. El lodo que resulta es parecido al lodo que se obtiene en los procesos de digestión aerobia convencionales. La digestión aerobia termófila representa un refinamiento adicional del proceso de digestión aerobia. Este proceso puede permitir conseguir altos rendimientos de eliminación de la fracción biodegradable (superiores al 80%) en tiempos de detención cortos (3 a 4 días), mediante la acción de bacterias termófilas a temperaturas entre 25º y 50º C superiores a la temperatura ambiente (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-7 col-8"},[_c('div',{staticClass:"d-lg-block d-none"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 21")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Tanque aerobio TAE ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/35.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Ajover (s.f.). http://www.ajover.co/wp-content/uploads/2019/02/fichatecnicasistemasepticoecoajoverajover.pdf")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta color-primario rounded-pill d-inline-block m-auto p-3 mb-4"},[_c('h4',{staticClass:"mb-0"},[_vm._v("Digestor anaerobio")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center m