e/view/545/1076 ")])]),_c('h4',[_vm._v("Características notables del proceso con biodiscos ")]),_c('p',[_vm._v("Notable elasticidad, es decir, es capaz de absorber elevadas puntas de carga orgánica e hidráulica, manteniendo muy estables los niveles de salida. No requiere recirculación de fangos; el fango activo del proceso es el que permanece adherido a los discos. Si la carga aumenta, la película bacteriana crece espontáneamente y los sólidos en suspensión a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los sólidos en el efluente al decantador secundario. No es necesario controlar la concentración del licor de mezcla, ya que el fango extraído es un desecho del proceso y ha de eliminarse como mejor convenga. ")])]),_c('div',{staticClass:"col-lg-6",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"d-lg-block d-none"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 28")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Esquema del sistema de biodiscos ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/47.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Delgado (2018). http://revistas.uni.edu.pe/index.php/tecnia/article/view/545/1076 ")])])])]),_c('h4',[_vm._v("Ruidos y otras variables ")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depósitos de oxidación son menores que los utilizados por otros procesos. El rendimiento del proceso es más estable en épocas frías, debido al hecho de permanecer cubiertos los tanques. El consumo energético es muy reducido y a igualdad de resultados, comparándolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.). ")]),_c('h4',[_vm._v("El funcionamiento de los biodiscos")]),_c('p',[_vm._v("Se divide en cuatro etapas, ver figura 29. ")]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"rounded-2 p-5",staticStyle:{"background-color":"#dee9f0"}},[_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 29")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v("Biodiscos")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/48.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Hidrometalica (s.f.). https://hidrometalica.com/biodiscos/ ")])])])])])]),_c('h4',[_vm._v("El agua bruta ")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-right"}},[_vm._v("El agua bruta de llegada entra a este tanque a través de la tubuladura de entrada, donde se produce una primera sedimentación de materia sólida. Los sólidos sedimentados se extraerán periódicamente por parte del usuario en la primera etapa donde se realiza la primera toma de contacto entre el agua bruta de entrada y los discos. La superficie de los discos se coloniza de forma natural, formando un recubrimiento visible. En la segunda etapa de contacto, gracias a la concepción del sistema de vertedero entre ambas etapas, se consigue un nivel de agua con menos fluctuaciones, favoreciendo el proceso y su salida hacia el “tanque de sedimentación secundaria”. Con el proceso de dos etapas se consigue aumentar el crecimiento de la biomasa de forma equilibrada y constante. En el tanque de sedimentación secundaria se produce la sedimentación de la materia sólida restante del proceso, a la cual se le realizan dos operaciones, la recirculación y extracción de sedimentos (Hidrometálica, s.f.). ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta color-primario rounded-pill d-inline-block m-auto p-3